html {
  display: flex;
  position: relative;
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.note {
  min-width: 380px;
}
.note svg {
  position: relative;
  top: 40px;
  fill: #223055;
  animation: bounce 0.8s ease infinite alternate;
  filter: drop-shadow(5px 5px 3px #ccc);
}
.note svg:nth-child(1) {
}
.note svg:nth-child(2) {
  animation-delay: 0.3s;
}
.note svg:nth-child(3) {
  animation-delay: 0.5s;
}
@keyframes bounce {
  100% {
    top: -40px;
  }
}
